import { useEffect, useState, useMemo } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  MODULES_ID,
  NOTIFICATION_TYPES,
} from "../../../../../../../contants/common";
import useDocsExtentions from "../../../../../../../redux/hooks/useDocsExtentions";
import ProcurementFileDetailsCard from "../../DetailsCard/Tabs/ProcurementFileDetailsCard";
import _, { remove } from "lodash";
import { shipmentFileDeleteEffect } from "../../../../../../../redux/shipment/ShipmentEffects";
import { setAppLoader } from "../../../../../../../redux/common/CommonActions";
import { triggerNotification } from "../../../../../../../utilz/utilz";
import { useLocation } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { routeFileSelector } from "../../../../../../../utilz/i18n";
import { procurementFileUploadEffect } from "../../../../../../../redux/Procurement/Project/ProcurementProjectEffects";
import Button from "../../../../../../components/Buttons/Button";
import { Col, Row } from "antd";
import HiddenFormInput from "../../../../../../components/Inputs/HiddenFormInput";

export default function FileDetails({  form }) {
  const dispatch = useDispatch();
  const notificationAPI = useSelector((state) => state.common.notificationAPI);
  const allowedExtentions = useDocsExtentions(MODULES_ID.SHIPMENT);
  const [documentsList, setDocumentsList] = useState([]);
  const { pathname: activeTab } = useLocation();
  const namespace = useMemo(() => routeFileSelector(activeTab), [activeTab]);
  const { t } = useTranslation([namespace, "translation"]);
  const { projectCopy } = useSelector((state) => state.procurementProject);


  useEffect(() => {
    const projectData = projectCopy?.data;

    if (projectData && Object.keys(projectData).length) {
      let temp = [];
      projectData?.projectDocumentList?.forEach((element) => {
        temp.push({
          ...element,
          fileGuid: true,
          docTypeCode: element?.docType || "",
          isNew: false,
          isUpdate: false,
          file: element.documentName,
          documentPath: element?.cloudUrl?.[0] || "",
          documentID: 0,
          projectID: 0,
          tenderID: 0,
          documentName: element.documentName || "",
          comments: element.documentTitle ? element.documentTitle : "",
          renderKey: _.uniqueId(),
        });
      });
      
      setDocumentsList(temp);
    }
  }, [projectCopy]);

  useEffect(() => {
    form.setFieldsValue({ documents: documentsList });
  }, [documentsList]);

  const handleNewFile = () => {
    let data = _.cloneDeep(documentsList);
    data = [{ renderKey: _.uniqueId(), isNew: true, isUpdate: true }, ...data];
    setDocumentsList(data);
  };

  const handleSave = async (formData, isNew, req) => {
    let documentsListTemp = [...documentsList];
    const findIndex = documentsListTemp.findIndex(
      (obj) => obj.renderKey === req.renderKey
    );
    documentsListTemp = [
      ...documentsListTemp.slice(0, findIndex),
      {
        ...documentsListTemp[findIndex],
        ...req,
        isNew: false,
        isUpdate: false,
      },
      ...documentsListTemp.slice(findIndex + 1),
    ];

    setDocumentsList(documentsListTemp);
  };

  const editRecord = (index) => {
    let array = [...documentsList];
    array[index]["isUpdate"] = true;
    setDocumentsList(array);
  };

  const handleDelete = async (filedetails, index) => {
    let temp = _.cloneDeep(documentsList);
    if (index !== null) {
      temp.splice(index, 1);
    }
    setDocumentsList(temp);
  };

  const handleReset = (index, isNew) => {
    let data = _.cloneDeep(documentsList);
    if (isNew) {
      let findObj = data.filter((item) => item.isNew != true);
      setDocumentsList(findObj);
    } else {
      let findObj = data[index];
      findObj.isUpdate = false;
      data[index] = findObj;
      setDocumentsList(data);
    }
  };

  return (
    <div className="d-flex flex-direction-column overflow-hidden h-100 br-4">
      <div className="custom-scroll-bar overflow-x-hidden mb-15 flex-1 pl-15 pr-15">
        <Row className="align-items-center mb-15" justify="space-between">
          <Col></Col>
          <Col>
            <Row justify="end" gutter={16}>
              <Col>
                <Button onClick={handleNewFile}>
                  {t("translation:default.documentManager.addNewFile")}
                </Button>
              </Col>
            </Row>
          </Col>
        </Row>
        {documentsList.map((row, index) => (
          <ProcurementFileDetailsCard
            key={row.renderKey}
            handleDelete={handleDelete}
            fileDetails={row}
            handleSave={handleSave}
            isNew={row.isNew}
            index={index}
            allowedExtentions={allowedExtentions}
            editRecord={editRecord}
            isProjectUpload={true}
            renderKey={row.renderKey}
            handleResetValueAction={(index, isNew) => handleReset(index, isNew)}
          />
        ))}
        {documentsList.length == 0 && (
          <div className="d-flex justify-content-center align-items-center h-100">
            {t("translation:default.notification.noRecondfound")}
          </div>
        )}
        <HiddenFormInput name="documents" />
      </div>
    </div>
  );
}
